/**
 * HTMLæ–‡å­—åˆ—ã‚’ã‚¨ã‚¹ã‚±ãƒ¼ãƒ—ã™ã‚‹
 * @param {string} str
 * @returns å¤‰æ›ã•ã‚ŒãŸç‰¹å®šã®è¨˜å·
 */
function escapeSpecialChars(str) {
  return str
    .replace(/&/g, "&amp;")
    .replace(/</g, "&lt;")
    .replace(/>/g, "&gt;")
    .replace(/"/g, "&quot;")
    .replace(/'/g, "&#039;");
}


/**
 * escapeSpecialCharsé–¢æ•°ã‚’HTMLæ–‡å­—åˆ—ã®ä¸­ã§å‘¼ã³å‡ºã™ ã‚¿ã‚°é–¢æ•°
 * @param {Array} strings
 * @param  {...any} values
 * @returns
 */
function escapeHTML(strings, ...values) {
  return strings.reduce((result, str, i) => {
    const value = values[i - 1];
    if (typeof value === 'string') {
      return result + escapeSpecialChars(value) + str;
    } else {
      return result + String(value) + str;
    }
  });
}

